import InputText from '/src/components/input-base/InputText'; //  
import InputPassword from '/src/components/input-base/InputPassword'; //  
import InputFile from '/src/components/input-base/InputFile'; //  
import InputFileSingle from '/src/components/input-base/InputFileSingle'; //  
import InputSwitch from '/src/components/input-base/InputSwitch'; //  switch
import InputRadio from '/src/components/input-base/InputRadio'; //  radio
import InputCheckbox from '/src/components/input-base/InputCheckbox'; //  checkbox
import InputTextarea from '/src/components/input-base/InputTextarea'; //  textarea
import InputPhone from '/src/components/input-base/InputPhone'; //  phone
import InputSearchSuggest from '/src/components/input-base/InputSearchSuggest'; //  search
import InputSearch from '/src/components/input-base/InputSearch'; //  search
import InputSelect from '/src/components/input-base/InputSelect'; //  select
import InputDate from '/src/components/input-base/InputDate'; //  date
import InputChoisePosts from '/src/components/input-base/InputChoisePosts'; //  select

// import InputComplex from '/src/components/input-base/InputComplex'; //  COMPLEX
import InputMulty from '/src/components/input-base/InputMulty'; //  EXTRA
// import InputAdd from '/src/components/input-base/InputAdd'; //  ADD

import InputAnimals from '/src/components/input-base/InputAnimals'; //  ADD
import InputAnimalSingle from '/src/components/input-base/InputAnimalSingle'; //  ADD

import InputTargetSale from '/src/components/input-base/InputTargetSale'; //  ADD
import InputClear from '/src/components/input-base/InputClear'; //  ADD
import InputChoiseBtn from '/src/components/input-base/InputChoiseBtn'; //  ADD
import InputRulesFromAcf from '/src/components/input-base/InputRulesFromAcf'; //  ADD
import InputPrice from '/src/components/input-base/InputPrice'; //  ADD
import InputChoisePostsPets from '/src/components/input-base/InputChoisePostsPets'; //  ADD
import InputChoisePostsCompany from '/src/components/input-base/InputChoisePostsCompany'; //  ADD
import InputEmodsy from '/src/components/input-base/InputEmodsy'; //  ADD


import { createValidateArr } from '/src/components/input/validator';




const choiseInputType = (obj) => {
  switch (obj.type) {
    case 'text': return <InputText obj={obj} />
    case 'password': return <InputPassword obj={obj} />
    case 'file': return <InputFile obj={obj} />
    case 'fileSingle': return <InputFileSingle obj={obj} />
    case 'switch': return <InputSwitch obj={obj} />
    case 'radio': return <InputRadio obj={obj} />
    case 'checkbox': return <InputCheckbox obj={obj} />
    case 'textarea': return <InputTextarea obj={obj} />
    case 'phone': return <InputPhone obj={obj} />
    case 'suggest': return <InputSearchSuggest obj={obj} />
    case 'search': return <InputSearch obj={obj} />
    case 'select': return <InputSelect obj={obj} />
    case 'date': return <InputDate obj={obj} />
    case 'multy': return <InputMulty obj={obj} />
    case 'animals': return <InputAnimals obj={obj} />
    case 'animalSingle': return <InputAnimalSingle obj={obj} />
    case 'posts': return <InputChoisePosts obj={obj} />
    case 'targetSale': return <InputTargetSale obj={obj} />
    case 'choiseBtn': return <InputChoiseBtn obj={obj} />
    case 'rulesFromAcf': return <InputRulesFromAcf obj={obj} />
    case 'price': return <InputPrice obj={obj} />
    case 'choisePets': return <InputChoisePostsPets obj={obj} />
    case 'choiseCompany': return <InputChoisePostsCompany obj={obj} />
    case 'emodsy': return <InputEmodsy obj={obj} />

    case 'clear': return <InputClear obj={obj} />
    default: return 'Empty Field';
  }
}

const RenderInput = ({ param }) => {
  return choiseInputType({ ...param, 'validate': createValidateArr(param.validate) });
}


export default RenderInput;
